import { motion } from "framer-motion";

const homeTransition = {
    type: "tween",
    ease: "anticipate",
    duration: 0.5
};

const pageVariants = {
    initial: {
        opacity: 0,
        y: "-5vh"
    },
    in: {
        opacity: 1,
        y: 0
    },
    out: {
        opacity: 0
    },
    initialPage: {
        opacity: 0,
        x: "-71vw",
        y: "12vh"
    },
    inPage: {
        opacity: 1,
        x: "-31vw",
        y: "12vh"
    },
    outPage: {
        opacity: 0
    }
};


export default function GetResults(days) {

    var seasonsImagePath;
    var result1 = ["The days provided aren't numbers or they're less than 0", ""];
    // var result2 = "The moon cycle";

    ((days) => {

        var limit = 20000;
        var daysN = Number(days);
        var seasons = {
            spring: {
                days: 20
            },
            summer: {
                days: 16
            },
            autumn: {
                days: 20
            },
            winter: {
                days: 15
            }
        }
        var yearsInDays = seasons.spring.days + seasons.summer.days + seasons.autumn.days + seasons.winter.days;

        if (isNaN(daysN)) return;
        if (daysN < 0) return;
        if (daysN > limit) return;

        // Gets the moon phase
        // var lastPhase;
        // var recentPhase;

        // for (var i = 1; i < days; i++) {

        //     if (i)

        // }

        // Transforms the excess days into normal ones
        if (daysN > yearsInDays) {

            var countOfYears = 0;

            for (var i = 0; i < days; i++) {

                var day = i;
                if (day === yearsInDays) continue;
                if (day % yearsInDays === 0) countOfYears += 1;

            }

            daysN = daysN - (yearsInDays * countOfYears);

        }

        // Seasons calculations
        {
            if (daysN === 0) {
                seasonsImagePath = "./Seasons/Autumn.jpg";
                result1 = ["Right now: Spring or Autumn,", `Winter in -21 days`];
            }
            
            // Spring/Autumn
            if (daysN < 21) {
                seasonsImagePath = "./Seasons/Autumn.jpg";
                result1 = ["Right now: Spring or Autumn,", `Winter in -${21 - daysN} days`];
            }

            // Winter
            if (daysN > 20 && daysN < 36) {
                seasonsImagePath = "./Seasons/Winter.jpg";
                result1 = ["Right now: Winter,", `Spring or Autumn in -${36 - daysN} days`];
            }

            // Spring/Autumn in the middle
            if (daysN > 35 && daysN < 55) {
                seasonsImagePath = "./Seasons/Spring.jpg";
                result1 = ["Right now: Spring or Autumn,", `Summer in -${56 - daysN} days`];
            }

            // Summer
            if (daysN > 55) {
                seasonsImagePath = "./Seasons/Summer.jpg";
                result1 = ["Right now: Summer,", `Spring or Autumn in -${72 - daysN} days`];
            }
        }

        // Moon cycles
        {

        }

    })(days)

    var resultsTableStyle = { height: "34vh" };
    var descriptionStyle = { marginTop: "-1.6vh" };
    var mobileImgStyle = {};

    if (window.innerWidth < 630) {
        resultsTableStyle.height = "31vh";
        mobileImgStyle.width = "59vw";
        descriptionStyle.padding = "0 5px";
    }

    return (
        <motion.div
            initial="initial"
            animate="in"
            exit="out"
            variants={pageVariants}
            transition={homeTransition}
        >
            <div className="resultsTable_Wrapper">
                <div className="resultsTable" style={resultsTableStyle}>
                    <div className="seasonsDiv">
                        <img style={mobileImgStyle} src={seasonsImagePath} alt="" />
                        <h4 style={{ marginTop: "2vh" }}>
                            {result1[0]}
                        </h4>
                        <h4 style={descriptionStyle}>
                            {result1[1]}
                        </h4>
                    </div>
                </div>
            </div>
        </motion.div>
    )

}